import { defineComponent, reactive, getCurrentInstance, h } from "vue";
import MonacoEditor from "./components/MonacoEditor";
import CodeParser from "./components/codeParser";
import Header from "./components/header";
import CodeView from "./components/codeView";
import DragLayout from "./components/dragLayout";

const appClasses = {
  quickWrap: {
    width: "1400px",
    margin: "0 auto",
    // background: "#fff",
  },
  quickContentSubTitle: {
    textAlign: "center",
    fontSize: "20px",
    color: "#fff",
    paddingTop: "40px",
  },
  controll_warp: {
    textAlign: "right",
  },
};
export default defineComponent({
  setup() {
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    const { classes } = window._createStyleSheet(appClasses);
    const createCodeFormData = (option: string) => {
      return `
      FromData.value = {
        account: "",
        bankType: null,
        othersAccount: "",
        amount: 0,
        remark: 1111,
        date: "2022-05-25",
        timeData: new Date(2000, 1, 1, 12, 0, 0),
        daterangeData: ["2022-05-01", "2022-05-05"],
        state: 2,
        checkboxData: [2],
        fileList: [],
        switchData: false,
      };
      QuickForm=${option}
      `;
    };
    const fullDemoCode = function (drag: boolean, edit: boolean) {
      return `
      FromData.value = {
        account: "",
        bankType: null,
        othersAccount: "",
        amount: 0,
        remark: 1111,
        date: "2022-05-25",
        timeData: new Date(2000, 1, 1, 12, 0, 0),
        daterangeData: ["2022-05-01", "2022-05-05"],
        state: 2,
        checkboxData: [2],
        fileList: [],
        switchData: false,
      };
      QuickForm = {
        formOptions: [
            {
        formElementLabel: "",
        formElementType: "select",
        key: "bankType",
        placeholder: "",
        options: [
          { label: "", value: 1 },
          { label: "", value: 2 },
          { label: "", value: 3 },
          { label: "", value: 4 },
        ],
      },
      {
        tirrgerEvents: ["mouseover"],
        formElementLabel: "",
        formElementType: "input",
        key: "account",
        placeholder: "",
      },
      {
        formElementLabel: "",
        formElementType: "input",
        key: "othersAccount",
        placeholder: "",
      },
      {
        formElementLabel: "",
        // formElementType: "container", //  type container
        childrenOptions: [
          {
            formElementType: "input:number",
            key: "amount", // mountModelValue: "amount", mountModelValue  key
          },
          {
            formElementType: "text",
            textValue: "",
            style: "margin-left: 10px; color:red",
          },
        ],
      },
      {
        formElementLabel: "",
        formElementType: "input:textarea",
        placeholder: "",
        key: "remark",
      },
      {
        formElementLabel: "",
        formElementType: "radio",
        key: "state",
        childrenOptions: [
          { label: 1, children: [""] },
          { label: 2, children: [""] },
        ],
      },
      {
        formElementLabel: "",
        directives: [["sif", "scope.state == 2"]],
        childrenOptions: [
          {
            formElementType: "date",
            key: "date",
            type: "date",
            placeholder: "",
            format: "YYYY/MM/DD",
            valueFormat: "YYYY-MM-DD",
          },
          {
            formElementType: "time",
            key: "timeData",
            type: "time",
            placeholder: "",
          },
        ],
      },
      {
        formElementLabel: "",
        formElementType: "text",
        textValue: "{{date}}   {{timeData}}",
      },
      {
        formElementLabel: "",
        formElementType: "checkbox",
        key: "checkboxData",
        childrenOptions: [
          { label: 1, children: [""] },
          { label: 2, children: [""] },
          { label: 3, children: [""] },
        ],
      },
      {
        formElementLabel: "",
        formElementType: "upload",
        key: "fileList",
        action: "#",
        listType: "picture-card",
        autoUpload: false,
        children: [
          {
            formElementType: "icon",
            children: ['']
          },
        ],
      },
      {
        formElementLabel: "",
        formElementType: "switch",
        key: "switchData"
      }
        ],
        layout: {
      labelWidth: 100,
      size: "default",
      labelPosition: "right",
    },
        drag: ${drag},
        edit: ${edit}
      };
      
      `;
    };
    const ins: any = getCurrentInstance();

    const State = reactive({
      RefCode: fullDemoCode(false, false),
      inputCode: `
        FromData.value = {
          inputValue1: "",
          inputValue1: 0,
          inputValue2: "",
        };
        QuickForm = {
          formOptions: [
            {
              formElementLabel: "text",
              formElementType: "input",
              key: "inputValue",
              placeholder: "placeholder",
            },
            {
              formElementLabel: "number",
              formElementType: "input:number",
              key: "inputValue1",
              placeholder: "placeholder",
            },
            {
              formElementLabel: "textarea",
              formElementType: "input:textarea",
              key: "inputValue2",
              placeholder: "placeholder",
            },
            {
              formElementLabel: "maxlengh",
              formElementType: "input:textarea",
              key: "inputValue2",
              maxlength:"100",
              showWordLimit: true,
              placeholder: "placeholder",
            },
          ],
          layout: {
            labelWidth: 100,
            size: "default",
            labelPosition: "right",
          }
        }
      `,
      btnCode: `
      QuickForm = {
        formOptions: [
          {
            formElementType: "button",
            formElementLabel: "",
            childrenOptions: [
              {
                formElementType: "button",
                children: ["default"]
              },
              {
                formElementType: "button",
                type:"primary",
                children: ["primary"]
              },
              {
                formElementType: "button",
                type:"success",
                children: ["success"]
              },
              {
                formElementType: "button",
                type: "info",
                children: ["info"]
              },
              {
                formElementType: "button",
                type: "danger",
                children: ["danger"]
              },
            ],
          },
          {
            formElementType: "button",
            formElementLabel: "",
            childrenOptions: [
              {
                formElementType: "button",
                children: ["default"],
                round: true
              },
              {
                formElementType: "button",
                type:"primary",
                children: ["primary"],
                round: true
              },
              {
                formElementType: "button",
                type:"success",
                children: ["success"],
                round: true
              },
              {
                formElementType: "button",
                type: "info",
                children: ["info"],
                round: true
              },
              {
                formElementType: "button",
                type: "danger",
                children: ["danger"],
                round: true
              },
            ],
          }
        ],
        layout: {
          labelWidth: 100,
          size: "default",
          labelPosition: "right",
        },
      }`,
      checkboxOrRadioCode: `
      FromData.value = {
        radio: 1,
        checkboxValue: []
      };
      QuickForm = {
        formOptions: [
          {
            formElementLabel: "",
            formElementType: "radio",
            key: "radio",
            childrenOptions: [
              { label: 1, children: ["1"]},
              { label: 2, children: ["2"]},
            ]
          },
          {
            formElementLabel: "",
            formElementType: "checkbox",
            key: "checkboxValue",
            childrenOptions: [
              { label: 1, children: ["1"]},
              { label: 2, children: ["2"]},
              { label: 3, children: ["3"]},
              { label: 4, children: ["4"]},
            ]
          },

        ],
        layout: {
          labelWidth: 100,
          size: "default",
          labelPosition: "right",
        },
      }
      `,
      selectCode: `
      FromData.value = {
        selectValue: "",
        multipleSelectValues: []
      };
      QuickForm = {
        formOptions: [
          {
            formElementLabel: "",
            formElementType: "select",
            key: "selectValue",
            placeholder: "",
            options: [
              { label: "1", value: 1 },
              { label: "2", value: 2 },
              { label: "3", value: 3 },
              { label: "4", value: 4 },
            ],
          },
          {
            formElementLabel: "",
            formElementType: "select",
            key: "multipleSelectValues",
            multiple: true,
            placeholder: "",
            options: [
              { label: "1", value: 1 },
              { label: "2", value: 2 },
              { label: "3", value: 3 },
              { label: "4", value: 4 },
            ],
          }
        ],
        layout: {
          labelWidth: 100,
          size: "default",
          labelPosition: "right",
        }
      }
      `,
      dragDemo: fullDemoCode(false, false),
      dragCompilte: false,
    });
    let CurrentDragCode: any = "";
    const Event = {
      toDrag: () => {
        State.dragCompilte = true;
        State.dragDemo = fullDemoCode(true, false);
      },
      compilte: () => {
        State.dragDemo = CurrentDragCode;
        State.dragCompilte = false;
      },
      codeChange(val: any) {
        CurrentDragCode = JSON.parse(val);
        CurrentDragCode.drag = false;
        CurrentDragCode = createCodeFormData(JSON.stringify(CurrentDragCode));
      },
      edit() {
        console.log("");
        State.dragDemo = fullDemoCode(false, true);
      },
    };
    // setInterval(() => {
    //   State.RefCode += 1;
    // }, 1000);
    // const FromData = reactive({
    //   daterange: ["2022-01-01", "2022-10-10"],
    // });
    // const QuickForm = {
    //   formOptions: [
    //     {
    //       formElementType: "container",
    //       childrenOptions: [
    //         { children: [""], formElementType: "container" },
    //         {
    //           formElementType: "date",
    //           format: "YYYY-MM-DD",
    //           key: "daterange",
    //           placeholder: "",
    //           type: "daterange",
    //           valueFormat: "YYYY-MM-DD",
    //         },
    //       ],
    //     },
    //     {
    //       tirrgerEvents: ["click"],
    //       formElementType: "button",
    //       children: [""],
    //       type: "primary",
    //       key: "add",
    //     },
    //   ],
    //   layout: {
    //     labelWidth: 100,
    //     size: "default",
    //     labelPosition: "right",
    //   },
    //   config: {
    //     renderType: "hander",
    //     containerVnode: h("div", {
    //       class: ["handler--container"],
    //     }),
    //     itemVnode: h("div", {
    //       class: ["handle--item"],
    //     }),
    //   },
    // };
    // const FormEvent = (params) => {
    //   console.log("event", params);
    // };
    // const FormChange = (params) => {
    //   console.log("change", params);
    // };
    return () => {
      return (
        <div class={classes.quickWrap}>
          <Header></Header>
          {/* <div>
            <quick-form
              form-data={FromData}
              quickOptions={QuickForm}
              onFormEvent={FormEvent}
              onFormChange={FormChange}
            ></quick-form>
          </div> */}

          <CodeView
            height={300}
            code={State.inputCode}
            title="--input--"
          ></CodeView>
          <CodeView
            height={300}
            code={State.selectCode}
            title="--select--"
          ></CodeView>
          <CodeView
            height={250}
            code={State.btnCode}
            title="--button--"
          ></CodeView>
          <CodeView
            height={450}
            code={State.checkboxOrRadioCode}
            title="--radio/checkbox--"
          ></CodeView>
          <CodeView
            height={800}
            code={State.RefCode}
            title="----"
          ></CodeView>
          <CodeView
            height={800}
            code={State.dragDemo}
            title="----"
            ref="dragView"
            onCodeChange={Event.codeChange}
          >
            <div class={classes.controll_warp}>
              {[
                !State.dragCompilte ? (
                  <el-button type="primary" onClick={Event.toDrag}>
                    
                  </el-button>
                ) : (
                  <el-button type="primary" onClick={Event.compilte}>
                    
                  </el-button>
                ),
                <el-button type="primary" onClick={Event.edit}>
                  
                </el-button>,
              ]}
            </div>
          </CodeView>
          <DragLayout></DragLayout>
        </div>
      );
    };
  },
});
